.container{
    width: 100%;
    margin: 0 auto;
}
    @media screen and (min-width: 480px){
    .container{
        max-width: 480px;
        }
    }
    @media screen and (min-width: 768px){
    .container{
        max-width: 768px;
       }
    }
    @media screen and (min-width: 1024px){
    .container{
        max-width: 1024px;
        }
    }